* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

html, body {
  min-width: 100%;
  min-height: 100%;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; }

::-moz-selection {
  background-color: palevioletred; }

::selection {
  background-color: palevioletred; }

.section {
  padding: 20px;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .section h2 {
    margin: 20px; }
    .section h2::before {
      content: '';
      display: inline-block;
      background-color: lightgreen;
      position: relative;
      width: 12px;
      height: 24px;
      margin-right: 8px; }

.header {
  width: 100%;
  min-height: 50vh;
  background-color: tomato;
  display: flex;
  flex-direction: row; }

.header-left {
  background-color: white;
  background-image: url("/assets/bg/funky-lines.png");
  box-shadow: inset -50px 0 100px -60px #333;
  flex-grow: 4; }

.header-right {
  background-color: #333;
  flex-grow: 6;
  position: relative;
  padding-bottom: 80px; }

.header-name {
  background-color: #fc76b4;
  display: block;
  position: relative;
  top: 60px;
  left: -40px;
  width: 100px; }
  .header-name h1 {
    -webkit-transform: translate(20px, -40px);
            transform: translate(20px, -40px);
    font-size: 3em;
    color: white; }

.header-deco {
  position: absolute;
  bottom: 10px;
  left: -40px;
  outline: dashed #fc76b4;
  width: 80%;
  height: 0; }

.header-link-wrapper {
  position: relative;
  left: 80px;
  top: -20px; }

.header-link {
  background-color: #333;
  color: white;
  padding: 4px;
  text-decoration: none;
  text-align: center;
  display: block;
  -webkit-transition: background-color ease 0.25s, color ease 0.25s;
  transition: background-color ease 0.25s, color ease 0.25s;
  background-color: white;
  color: black;
  margin-bottom: 10px;
  padding: 10px; }
  .header-link:hover {
    background-color: lightcyan;
    color: #333; }
  .header-link:active {
    background-color: lightgreen;
    color: #333; }

.projects {
  display: flex;
  margin-bottom: 20px;
  justify-content: center;
  flex-wrap: wrap; }

.card {
  margin: 40px 40px 140px;
  max-width: 300px;
  position: relative; }

.card-img {
  position: relative;
  z-index: 2;
  background-color: #eee;
  box-shadow: -6px 6px 14px 0 #aaa;
  width: 300px;
  height: 300px;
  max-width: 100%;
  max-height: 100%; }
  .card-img img {
    width: 100%; }

.btn-wrapper {
  position: absolute;
  left: 30%;
  top: 20px;
  width: 80%; }

.card-link {
  background-color: #333;
  color: white;
  padding: 4px;
  text-decoration: none;
  text-align: center;
  display: block;
  -webkit-transition: background-color ease 0.25s, color ease 0.25s;
  transition: background-color ease 0.25s, color ease 0.25s;
  box-shadow: -6px 6px 10px -5px lightgreen;
  margin: 0 0 4px;
  z-index: 3;
  position: relative; }
  .card-link:hover {
    background-color: lightcyan;
    color: #333; }
  .card-link:active {
    background-color: lightgreen;
    color: #333; }

.card-title {
  background-color: #333;
  color: lightgreen;
  position: absolute;
  padding: 10px;
  left: -30px;
  bottom: 10px;
  z-index: 3; }

.card-body {
  background-color: #ebebeb;
  position: absolute;
  padding: 20px;
  display: flex;
  align-items: flex-end;
  min-height: 100%;
  top: 110px;
  left: 30px;
  width: 100%;
  z-index: 1; }

.card-label {
  background-color: lightgreen;
  color: #333;
  position: absolute;
  font-size: 0.8em;
  padding: 2px;
  left: -10px;
  bottom: -5px;
  z-index: 3; }

